// app/admin/chat/page.tsx
'use client';
import React, { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/app/components/ui/button';
import { Input } from '@/app/components/ui/input';
import { ScrollArea } from '@/app/components/ui/scroll-area';
import { Alert, AlertDescription } from '@/app/components/ui/alert';
import { 
  MessageCircle, 
  Send, 
  Loader2, 
  User, 
  Sparkles,
  Calendar, 
  Clock,
  MapPin,
  Lightbulb,
  LogIn,
  ExternalLink
} from 'lucide-react';

interface Message {
  id: string;
  content: string;
  type: 'user' | 'ai';
  timestamp: Date;
  isLoading?: boolean;
}

interface BirthDetails {
  date: string;
  time: string;
  place: string;
  latitude?: string;
  longitude?: string;
}

// Default welcome message
const WELCOME_MESSAGE = "Welcome to your Parasara Jyotish astrological consultation. I'm here to provide insights based on Vedic astrology principles. How can I assist you today?";

// Default responses when backend is not available
const DEFAULT_RESPONSES = [
  "Based on traditional Vedic astrology, your birth chart suggests a period of transformation. Saturn's current transit indicates a time of growth through structured effort.",
  "Looking at the Parasara system, your chart shows Jupiter's influence bringing expansion and wisdom to your current situation.",
  "According to Jyotish principles, the position of Mars in your chart indicates increased energy and drive in your endeavors this month.",
  "Your Venus placement in the birth chart suggests harmony in relationships. This is a good time to strengthen personal connections.",
  "The Moon's position in your chart reveals strong intuitive abilities. Trust your inner guidance during this period.",
  "Mercury's influence in your chart points to favorable communication aspects. This is an excellent time for learning and sharing knowledge."
];

// Get a response based on user query keywords
const getResponseForQuery = (query: string) => {
  const lowerQuery = query.toLowerCase();
  
  if (lowerQuery.includes('career') || lowerQuery.includes('job') || lowerQuery.includes('work') || lowerQuery.includes('profession')) {
    return "According to your birth chart, your 10th house indicates strengths in leadership and analytical thinking. The current planetary positions suggest favorable conditions for career growth, especially in areas requiring technical expertise or management skills.";
  }
  
  if (lowerQuery.includes('love') || lowerQuery.includes('relationship') || lowerQuery.includes('marriage') || lowerQuery.includes('partner')) {
    return "Your birth chart shows Venus in a harmonious position, suggesting a natural ability to form meaningful connections. The 7th house indicates you value balance and mutual respect in relationships. The next few months show favorable aspects for strengthening bonds.";
  }
  
  if (lowerQuery.includes('money') || lowerQuery.includes('finance') || lowerQuery.includes('wealth') || lowerQuery.includes('financial')) {
    return "Looking at your 2nd and 11th houses, your chart indicates good potential for financial growth. Jupiter's position suggests expansion, though Saturn's influence reminds you to maintain discipline in spending. The next quarter appears favorable for calculated investments.";
  }
  
  if (lowerQuery.includes('health') || lowerQuery.includes('wellness') || lowerQuery.includes('well-being')) {
    return "Your birth chart indicates generally good vitality from your Sun position. Mars suggests you benefit from regular physical activity, while Saturn indicates the importance of establishing consistent routines. Pay particular attention to stress management during this period.";
  }
  
  if (lowerQuery.includes('family') || lowerQuery.includes('home') || lowerQuery.includes('parent') || lowerQuery.includes('child')) {
    return "The 4th house in your chart shows strong family connections. The current planetary alignment suggests a period of harmony at home, with opportunities to strengthen family bonds. Jupiter's influence brings growth and expansion to your domestic sphere.";
  }
  
  // Default to random response if no keywords match
  return DEFAULT_RESPONSES[Math.floor(Math.random() * DEFAULT_RESPONSES.length)];
};

const ChatPage = () => {
  const router = useRouter();
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [birthDetails, setBirthDetails] = useState<BirthDetails | null>(null);
  const [initializing, setInitializing] = useState(true);
  const [conversationId, setConversationId] = useState<string | null>(null);
  const [offlineMode, setOfflineMode] = useState(false);
  const [authDebug, setAuthDebug] = useState<string[]>([]);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Add debug info
  const addDebugInfo = (info: string) => {
    setAuthDebug(prev => [...prev, `${new Date().toISOString().split('T')[1].slice(0, 8)}: ${info}`]);
  };

  // Check authentication and fetch birth details when component mounts
  useEffect(() => {
    const checkAuth = async () => {
      addDebugInfo("Starting authentication check");
      const token = localStorage.getItem('token');
      
      if (!token) {
        addDebugInfo("No token found in localStorage");
        localStorage.setItem('redirect_reason', 'no_token');
        router.push('/auth/login');
        return;
      }
      
      addDebugInfo(`Token found: ${token.substring(0, 10)}...`);

      try {
        // Add welcome message immediately to improve perceived performance
        setMessages([{
          id: Date.now().toString(),
          content: WELCOME_MESSAGE,
          type: 'ai',
          timestamp: new Date()
        }]);

        // Get user profile with birth details
        addDebugInfo("Fetching user profile");
        const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/user/profile`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        addDebugInfo(`Profile response status: ${response.status}`);

        if (response.status === 401) {
          // Unauthorized - token might be expired or invalid
          addDebugInfo("Token unauthorized (401)");
          localStorage.removeItem('token'); // Clear the invalid token
          localStorage.setItem('redirect_reason', 'invalid_token');
          router.push('/auth/login');
          return;
        }

        if (!response.ok) {
          addDebugInfo(`Profile fetch error: ${response.status}`);
          
          // For other errors, we'll use offline mode instead of redirecting
          setOfflineMode(true);
          setError("Unable to fetch profile. Using offline mode.");
          setInitializing(false);
          return;
        }

        const userData = await response.json();
        addDebugInfo(`User data received: ${JSON.stringify(userData).substring(0, 100)}...`);
        
        // Check if birth details exist - don't redirect for now, just operate in local mode
        if (!userData.birth_details || !userData.birth_details.date) {
          addDebugInfo("No birth details found");
          setOfflineMode(true);
          setError("Birth details not found. Some features may be limited.");
        } else {
          addDebugInfo("Birth details found");
          setBirthDetails(userData.birth_details);
          
          // Try to initialize chat but don't block on it
          try {
            await startConversation(userData.birth_details);
          } catch (err) {
            addDebugInfo(`Error starting conversation: ${err}`);
            setOfflineMode(true);
          }
        }
        
      } catch (err) {
        addDebugInfo(`Error during initialization: ${err}`);
        
        // Fall back to offline mode with a welcome message
        setOfflineMode(true);
        setError("Unable to connect to server. Using offline mode.");
      } finally {
        setInitializing(false);
      }
    };

    checkAuth();
  }, [router]);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const startConversation = async (birthDetails: BirthDetails) => {
    addDebugInfo("Starting conversation");
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        addDebugInfo("No token found when starting conversation");
        return;
      }
      
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat/start`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          birth_details: birthDetails
        })
      });

      addDebugInfo(`Start conversation response: ${response.status}`);

      if (response.status === 401) {
        // Token expired during the session
        addDebugInfo("Token unauthorized (401) during conversation start");
        return;
      }

      if (!response.ok) {
        // Switch to offline mode for other errors
        addDebugInfo(`Error response: ${response.status}`);
        setOfflineMode(true);
        return;
      }

      const data = await response.json();
      addDebugInfo(`Conversation data received: ID=${data.conversation_id}`);
      
      // Save conversation ID for future messages
      setConversationId(data.conversation_id);
      
      // Update AI welcome message if needed
      if (data.initial_response && data.initial_response !== WELCOME_MESSAGE) {
        setMessages([{
          id: Date.now().toString(),
          content: data.initial_response,
          type: 'ai',
          timestamp: new Date()
        }]);
      }
      
    } catch (err: any) {
      addDebugInfo(`Error in startConversation: ${err.message}`);
      setOfflineMode(true);
    }
  };

  const handleSendMessage = async () => {
    if (!input.trim() || loading) return;
    
    const userMessage: Message = {
      id: Date.now().toString(),
      content: input,
      type: 'user',
      timestamp: new Date()
    };
    
    // Store the user's message for use in the catch block
    const userQuery = input;
    
    // Add a placeholder for the AI response
    const placeholderId = (Date.now() + 1).toString();
    
    setMessages(prev => [...prev, userMessage, {
      id: placeholderId,
      content: '',
      type: 'ai',
      timestamp: new Date(),
      isLoading: true
    }]);
    
    setInput('');
    setLoading(true);
    setError('');
    
    // If we're in offline mode, return a custom response
    if (offlineMode) {
      setTimeout(() => {
        // Replace placeholder with custom response
        setMessages(prev => 
          prev.map(msg => 
            msg.id === placeholderId ? {
              ...msg,
              content: getResponseForQuery(userQuery),
              isLoading: false
            } : msg
          )
        );
        setLoading(false);
      }, 1500);
      return;
    }
    
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        addDebugInfo("No token found when sending message");
        setOfflineMode(true);
        throw new Error('Authentication required');
      }
      
      addDebugInfo(`Sending message: ${userQuery.substring(0, 30)}...`);
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat/continue`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          message: userQuery,
          conversation_id: conversationId
        })
      });
      
      addDebugInfo(`Continue response status: ${response.status}`);
      
      if (response.status === 401) {
        // Now we'll use offline mode rather than redirecting
        addDebugInfo("Token unauthorized (401) during message send");
        setOfflineMode(true);
        throw new Error('Using offline mode due to authentication issue');
      }
      
      if (!response.ok) {
        // Switch to offline mode for server errors
        addDebugInfo(`Error response: ${response.status}`);
        setOfflineMode(true);
        throw new Error('Server error - using offline mode');
      }
      
      const data = await response.json();
      addDebugInfo("Response data received");
      
      // Replace placeholder with actual response
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: data.response || "I understand your question. Let me share some insights based on Vedic astrology principles.",
            isLoading: false
          } : msg
        )
      );
      
    } catch (err: any) {
      addDebugInfo(`Error in handleSendMessage: ${err.message}`);
      
      // Use offline mode instead of redirecting to login
      setOfflineMode(true);
      
      // Replace placeholder with custom response
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: getResponseForQuery(userQuery),
            isLoading: false
          } : msg
        )
      );
      
      setError("Switched to offline mode. Responses are not personalized to your birth chart.");
      
      // Clear error after a few seconds
      setTimeout(() => {
        setError('');
      }, 5000);
    } finally {
      setLoading(false);
    }
  };

  // Handle Enter key press
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  // Handle login redirect
  const handleLoginRedirect = () => {
    router.push('/auth/login');
  };

  // Handle debug toggle
  const toggleDebug = () => {
    const debugElement = document.getElementById('auth-debug');
    if (debugElement) {
      debugElement.style.display = debugElement.style.display === 'none' ? 'block' : 'none';
    }
  };

  if (initializing) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center space-y-4">
          <Loader2 className="h-12 w-12 animate-spin mx-auto text-purple-600" />
          <p className="text-gray-600">Initializing your astrological consultation...</p>
          <div className="text-xs text-gray-400 mt-4">
            <Button variant="link" size="sm" onClick={toggleDebug}>Debug Info</Button>
            <div id="auth-debug" className="text-left bg-gray-100 p-2 rounded mt-2 overflow-y-auto max-h-40" style={{display: 'none'}}>
              {authDebug.map((log, i) => (
                <div key={i} className="text-xs">{log}</div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-screen bg-gray-50">
      {/* Debug panel (hidden by default) */}
      <div id="auth-debug" className="bg-gray-100 p-2 border-b text-xs overflow-y-auto max-h-40" style={{display: 'none'}}>
        <h3 className="font-bold">Auth Debug Info:</h3>
        {authDebug.map((log, i) => (
          <div key={i}>{log}</div>
        ))}
      </div>
      
      {/* Header with birth details */}
      <div className="bg-white border-b border-gray-200 p-4">
        <div className="max-w-5xl mx-auto flex flex-col md:flex-row justify-between items-center">
          <div className="flex items-center mb-4 md:mb-0">
            <div className="bg-purple-100 p-2 rounded-full mr-3">
              <Sparkles className="h-6 w-6 text-purple-600" />
            </div>
            <h1 className="text-xl font-semibold">Astrological Consultation</h1>
            {offlineMode && (
              <span className="ml-2 text-xs px-2 py-1 bg-amber-100 text-amber-800 rounded-full">
                Offline Mode
              </span>
            )}
            <Button variant="ghost" size="sm" className="ml-2" onClick={toggleDebug}>
              <span className="sr-only">Debug</span>
              <ExternalLink className="h-3 w-3" />
            </Button>
          </div>
          
          {birthDetails ? (
            <div className="flex flex-wrap gap-3 text-sm text-gray-500">
              <div className="flex items-center">
                <Calendar className="h-4 w-4 mr-1" />
                <span>{birthDetails.date}</span>
              </div>
              <div className="flex items-center">
                <Clock className="h-4 w-4 mr-1" />
                <span>{birthDetails.time}</span>
              </div>
              <div className="flex items-center">
                <MapPin className="h-4 w-4 mr-1" />
                <span>{birthDetails.place}</span>
              </div>
            </div>
          ) : offlineMode && (
            <Button 
              variant="outline" 
              size="sm"
              onClick={handleLoginRedirect}
              className="text-sm"
            >
              <LogIn className="h-4 w-4 mr-1" />
              Re-login
            </Button>
          )}
        </div>
      </div>
      
      {/* Main chat area */}
      <div className="flex-1 overflow-hidden">
        <div className="max-w-5xl h-full mx-auto flex flex-col">
          <ScrollArea className="flex-1 p-4">
            <div className="space-y-6">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div 
                    className={`flex items-start space-x-2 max-w-[80%] ${
                      message.type === 'user' ? 'flex-row-reverse space-x-reverse' : ''
                    }`}
                  >
                    <div className={`p-2 rounded-full ${
                      message.type === 'user' ? 'bg-purple-100' : 'bg-blue-100'
                    }`}>
                      {message.type === 'user' ? 
                        <User className="h-5 w-5 text-purple-600" /> : 
                        <Sparkles className="h-5 w-5 text-blue-600" />
                      }
                    </div>
                    <div 
                      className={`p-4 rounded-lg ${
                        message.type === 'user' 
                          ? 'bg-purple-600 text-white' 
                          : 'bg-white border border-gray-200'
                      }`}
                    >
                      {message.isLoading ? (
                        <div className="flex items-center space-x-2">
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-0"></div>
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-150"></div>
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-300"></div>
                        </div>
                      ) : (
                        <div className="prose max-w-none" dangerouslySetInnerHTML={{ 
                          __html: message.content.replace(/\n/g, '<br>') 
                        }} />
                      )}
                    </div>
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
          </ScrollArea>
          
          {/* Input area */}
          <div className="border-t border-gray-200 bg-white p-4">
            {error && (
              <Alert variant="default" className="mb-4 bg-amber-50">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            
            <div className="flex space-x-2">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyDown={handleKeyPress}
                placeholder="Ask any question about your astrological chart..."
                disabled={loading || initializing}
                className="flex-1"
              />
              <Button 
                onClick={handleSendMessage}
                disabled={loading || initializing || !input.trim()}
                className="bg-purple-600 hover:bg-purple-700"
              >
                {loading ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <Send className="h-5 w-5" />
                )}
              </Button>
            </div>
            
            {/* Suggestion chips */}
            <div className="flex flex-wrap gap-2 mt-3">
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What does my birth chart reveal about my career path?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Career insights
              </Button>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What planetary positions influence my relationships?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Relationship analysis
              </Button>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What remedies would help balance my chart's challenges?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Remedial suggestions
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatPage;